import React from 'react';
import moment from 'moment';
import { useDispatch } from 'react-redux';
import { startEliminar, startSetActive } from '../../actions/clientes';
import {isMobile} from 'react-device-detect';
import { useHistory } from 'react-router-dom';

export const ClientesAllItems = ({clientes,loading}) => {
  const dispatch = useDispatch();
  const history = useHistory();
    if(loading){
        return <h2>Cargando...</h2>
    }
    const handleGoEliminar = (cid) => {
      dispatch(startEliminar(cid));
      setTimeout(()=>{
        history.push(`/controlclientes`);
      },500);
  };
    const handleGoEditar = (uid) => {
      dispatch(startSetActive(uid));
      setTimeout(()=>{
        history.push(`/editarclienteadmin/${uid}`);
      },500);
    };
  return (
    <>
      {clientes && <table className={isMobile ? "d-block overflow-scroll table table-sm table-bordered table-striped":"table table-sm table-bordered table-striped"}>
    <thead className="bg-dark text-light">
      <tr>
        <th className="text-center align-middle small" scope="col">DNI</th>
        <th className="text-center align-middle small" scope="col">Fecha de carga</th>
        <th className="text-center align-middle small" scope="col">Nombre</th>
        <th className="text-center align-middle small" scope="col">Apellido</th>
        <th className="text-center align-middle small" scope="col">Celular</th>
        <th className="text-center align-middle small" scope="col">Estado</th>
        <th className="text-center align-middle small" scope="col">Ticket</th>
        <th className="text-center align-middle small" scope="col">Free</th>
        <th className="text-center align-middle small" scope="col">Eventos</th>
        <th className="text-center align-middle small" scope="col">Usuario</th>
        <th className="text-center align-middle small" scope="col">Editar</th>
        <th className="text-center align-middle small" scope="col">Eliminar</th>
      </tr>
    </thead>
    <tbody>
    {clientes.map(item => {
        return (
          <tr key={item.uid}>
            <td className="text-center small">{ item.dni }</td>
            <td className="text-center small">{ moment(item.fecha).format('DD-MM-YYYY') }</td>
            <td className="text-center small">{ item.nombre }</td>
            <td className="text-center small">{ item.apellido }</td>
            <td className="text-center small">{ item.celular }</td>
            <td className="text-center small">{ item.estado?<i style={{"color":"green"}} className="fas fa-check"></i>: <i style={{"color":"red"}} className="fas fa-close"></i>}</td>
            <td className="text-center small">{ item.esticket?<i style={{"color":"green"}} className="fas fa-check"></i>: <i style={{"color":"red"}} className="fas fa-close"></i>}</td>
            <td className="text-center small">{ item.free?<i style={{"color":"green"}} className="fas fa-check"></i>: <i style={{"color":"red"}} className="fas fa-close"></i>}</td>
            <td className="text-center small">
            {item.entradas.map(entrada => {
                return entrada? <pre key={item.uid}>{entrada.nombre} - ${entrada.importe} - {entrada.tipo}</pre> : <pre key={item.uid}>-</pre>
            })}
            </td>
            <td className="text-center small">{ item.usuario?item.usuario.nombre+" "+item.usuario.apellido:"-" }</td>
            <td className="text-center small"><button className='btn btn-warning' onClick={()=>handleGoEditar(item.uid)}>Editar</button></td>
            <td className="text-center small"><button className='btn btn-danger' onClick={()=>handleGoEliminar(item.uid)}><i className="fas fa-trash-alt"></i></button></td>
          </tr>
        );
      })}
    </tbody>
  </table>}
  </> );
};
